.seed-wall-wrap {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0 16px;
  flex-direction: column;
  justify-content: space-around;
}
.seed-wall-wrap .row {
  display: flex;
  justify-content: space-between;
}
.seed-wall-wrap .grid {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  border-radius: 8px;
  text-align: center;
  color: #EEEFF1;
  background: #EEEFF1;
  cursor: pointer;
}
.seed-wall-wrap .active {
  color: #fff;
  box-shadow: 0 3px 10px 0 #E97050;
  background: linear-gradient(#EAAD55, #E97050);
}
.seed-wall-wrap .default {
  color: #5D678A;
  background: #EEEFF1;
}
.seed-popover-wrap {
  padding: 4px 8px;
}
.seed-popover-wrap .item {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.seed-popover-wrap .item .left {
  display: flex;
  min-width: 72px;
}
.seed-popover-wrap .item .left .ant-badge-status-text {
  color: #172D4D;
}
.seed-popover-wrap .item .right {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 88px;
  height: 32px;
  background: #F4F5F7;
}
.seed-popover-wrap .item .right .text-bold {
  font-weight: bold;
}
.seed-popover-wrap .item .right .text-light {
  color: #9DA2B6;
}
.seed-popover-wrap .item .abnormal {
  font-weight: bold;
  color: #DC4164;
  background: #FFDCE4;
}
.seed-popover-wrap .item .justify .ant-badge-status-text {
  display: inline-block;
  width: 56px;
  text-align-last: justify;
}
.seed-popover-wrap .item-last {
  display: inherit;
}
.seed-popover-wrap .item-last .detail-product {
  width: 100%;
  margin-top: 8px;
}
.seed-popover-wrap .bold {
  font-weight: bold;
}
.seed-popover-wrap .light {
  color: #9DA2B6;
}
