.splitter-componented {
  position: relative;
  background: #fff;
}
.splitter-componented > .layout-pane > .splitter-item {
  width: 100%;
  height: 100%;
}
.splitter-componented > .layout-pane > .splitter-item > .preview-secondary-content {
  position: absolute;
  background: #fff;
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item {
  display: flex;
  align-items: center;
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item > .fold-panel {
  width: 26px;
  height: 100%;
  padding: 6px;
  user-select: none;
  cursor: pointer;
  background: #EDEEEE;
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item > .fold-panel > p {
  position: relative;
  top: 46px;
  letter-spacing: 3px;
  white-space: nowrap;
  font-size: 13px;
  margin-bottom: 0;
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item > .fold-panel-left {
  left: 0;
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item > .fold-panel-left > p {
  transform: rotate(90deg);
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item > .fold-panel-right {
  right: 0;
}
.splitter-componented > .layout-pane .splitter-horizontal.splitter-item > .fold-panel-right > p {
  transform: rotate(-90deg);
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item > .fold-panel {
  display: flex;
  align-items: center;
  width: 100%;
  height: 26px;
  padding: 6px;
  background: #EDEEEE;
  cursor: pointer;
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item > .fold-panel > p {
  position: relative;
  margin-left: 4px;
  letter-spacing: 3px;
  white-space: nowrap;
  font-size: 13px;
  margin-bottom: 0;
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item > .fold-panel-top {
  bottom: 0;
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item > .fold-panel-top > i {
  transform: rotate(90deg);
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item > .fold-panel-bottom {
  top: 0;
}
.splitter-componented > .layout-pane .splitter-vertical.splitter-item > .fold-panel-bottom > i {
  transform: rotate(90deg);
}
.splitter-componented > .preview-secondaryDom-horizontal {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 9;
  background: #fff;
}
.splitter-componented > .preview-secondaryDom-vertical {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 9;
  background: #fff;
}
.splitter-componented > .layout-splitter {
  position: relative;
  width: 16px;
  background: #F0F2F5;
}
.splitter-componented > .layout-splitter:hover {
  background: #F0F2F5;
}
.splitter-componented > .layout-splitter > .splitter-button {
  position: absolute;
  cursor: pointer;
}
.splitter-componented > .layout-splitter > .right {
  top: 50%;
  transform: translateY(-50%) rotate(180deg);
}
.splitter-componented > .layout-splitter > .left {
  top: 50%;
  transform: translateY(-50%) rotate(0deg);
}
.splitter-componented > .layout-splitter > .top {
  top: -16px;
  left: 50%;
  transform: translateX(-50%) rotate(90deg);
}
.splitter-componented > .layout-splitter > .bottom {
  top: -16px;
  left: 50%;
  transform: translateX(-50%) rotate(-90deg);
}
.splitter-componented.layout-changing > .layout-splitter {
  background: #F0F2F5;
}
.splitter-componented.splitter-layout.splitter-layout-vertical > .layout-splitter {
  height: 16px;
}
