.ag-grid-block {
  position: relative;
}
.ant-menu.ag-grid-context-menu {
  position: absolute;
  top: 30px;
  left: 30px;
  border: 1px solid #e8e8e8;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.ant-menu.ag-grid-context-menu > .ant-menu-item {
  height: 30px;
  line-height: 30px;
  width: 160px;
  padding-left: 40px;
  position: relative;
}
.ant-menu.ag-grid-context-menu > .ant-menu-item .menu-item-icon {
  position: absolute;
  top: 9px;
  left: 16px;
}
.ag-row .cell-end-edit-icon {
  visibility: hidden;
  width: 20px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-top: 0.125em;
  color: #999;
}
.ag-row .cell-render-text {
  padding-right: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.ag-row.ag-row-selected .cell-end-edit-icon,
.ag-row .ag-row-hover .cell-end-edit-icon {
  visibility: visible;
}
