.text-normal {
  color: #5D678A;
  font-family: Alibaba-PuHuiTi-R;
  font-size: 14px;
  letter-spacing: 0;
}
.text-bold {
  color: #172D4D;
  font-family: Alibaba-PuHuiTi-R;
  font-size: 14px;
  letter-spacing: 0;
  font-weight: bold;
}
.text-warn {
  font-family: Alibaba-PuHuiTi-R;
  font-size: 14px;
  color: #E97050;
  letter-spacing: 0;
}
.font-18 {
  font-size: 18px;
}
.font-24 {
  font-size: 24px;
}
.badge .ant-badge-status-text {
  font-family: Alibaba-PuHuiTi-R;
  font-size: 14px;
  color: #5D678A;
  letter-spacing: 0;
}
.modal-content-grid .ant-modal-header,
.modal-content-grid .rnd-modal-header {
  border-bottom: 0;
}
.modal-content-grid .ant-modal-header .ant-modal-title,
.modal-content-grid .rnd-modal-header .ant-modal-title,
.modal-content-grid .ant-modal-header .rnd-modal-title,
.modal-content-grid .rnd-modal-header .rnd-modal-title {
  font-family: Alibaba-PuHuiTi-M;
  font-size: 16px;
  color: #172D4D;
  letter-spacing: 0;
}
.modal-content-grid .ant-modal-footer,
.modal-content-grid .rnd-modal-footer {
  background: #F5F8FB;
}
.modal-content-grid .ant-modal-footer button,
.modal-content-grid .rnd-modal-footer button {
  width: 96px;
}
.modal-content-grid .ant-modal-footer button:nth-child(2),
.modal-content-grid .rnd-modal-footer button:nth-child(2) {
  margin-left: 24px;
}
.modal-content-normal .ant-modal-header {
  padding: 12px 24px;
}
.modal-content-normal .ant-modal-header .ant-modal-title {
  opacity: 0.85;
  font-family: Alibaba-PuHuiTi-M;
  font-size: 16px;
  color: #172D4D;
  line-height: 24px;
}
.modal-content-normal .ant-modal-body {
  padding: 40px 40px 32px 40px;
  background: #F5F8FB;
  font-family: Alibaba-PuHuiTi-R;
  font-size: 14px;
  color: #172D4D;
  letter-spacing: 0;
}
.modal-content-normal .ant-modal-footer {
  padding: 0 24px 24px 0;
  border: none;
  background: #F5F8FB;
}
.modal-content-normal .ant-modal-footer button:nth-child(2) {
  margin-left: 24px;
}
.seed-layout {
  padding-bottom: 64px;
  display: flex;
}
.seed-layout .card-header .ant-card-body {
  padding: 0;
}
.seed-layout .card-header .seed-form {
  display: flex;
  align-items: center;
}
.seed-layout .card-header .seed-form .col-first {
  padding-left: 32px;
}
.seed-layout .card-header .seed-form .col-third {
  padding-left: 40px;
}
.seed-layout .card-header .seed-form .col-second,
.seed-layout .card-header .seed-form .col-four {
  min-width: 200px;
  padding: 8px 16px;
  border-radius: 4px;
  background: #E6ECF2;
}
.seed-layout .card-header .seed-form .col-second .wave-type,
.seed-layout .card-header .seed-form .col-four .wave-type {
  font-size: 24px;
  color: #E97050;
  font-weight: bold;
}
.seed-layout .card-header .seed-form .col-second .mb-10,
.seed-layout .card-header .seed-form .col-four .mb-10 {
  margin-bottom: 10px;
}
.seed-layout .card-header .seed-form .col-five {
  display: flex;
  align-items: center;
  text-align: right;
  justify-content: flex-end;
}
.seed-layout .card-header .seed-form .col-five .number {
  display: flex;
  width: 104px;
  height: 104px;
  font-size: 66px;
  margin-left: 20px;
  border-radius: 4px;
  color: #fff;
  align-items: center;
  justify-content: center;
  background: #E97050;
  font-family: 'AlibabaSans-Heavy';
  font-weight: bold;
}
.seed-layout .card-header .seed-form .input-seed {
  width: 160px;
}
.seed-layout .card-header .seed-form .input-seed-second {
  width: 208px;
}
.seed-layout .card-body {
  height: 400px;
  flex: 1;
  margin-top: 14px;
}
.seed-layout .card-body .seed-grid {
  margin-top: 16px;
}
.seed-layout .card-body .ant-card-body {
  width: 100%;
  height: 100%;
  padding: 0;
}
.seed-layout .card-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  box-shadow: 0 -2px 8px 0 rgba(157, 162, 182, 0.28), inset 0 1px 0 0 #DCE1E6;
}
.seed-layout .card-footer .left-text {
  display: flex;
  padding-left: 48px;
}
.seed-layout .card-footer .right-group {
  text-align: right;
  padding-left: 88px;
  position: relative;
  top: 6px;
}
.seed-layout .card-footer .right-group button {
  margin-right: 24px;
}
.seed-layout .card-footer .right-group .button-default {
  background: #FFFFFF;
  border: 1px solid #0265FF;
  border-radius: 3px;
  color: #0265FF;
}
.seed-layout .card-footer .ant-card-body {
  padding: 10px;
}
