.erp-pagination {
  margin: 5px 0px;
  height: 24px;
  min-width: 740px;
  text-align: center;
}
.erp-pagination > div {
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;
}
.erp-pagination .page-btn {
  color: #fff;
  background-color: #2790F8;
  padding: 0px 6px;
  border-radius: 9px;
  cursor: pointer;
}
.erp-pagination .page-column {
  cursor: pointer;
}
.erp-pagination .page-column > i {
  margin-right: 3px;
}
.erp-pagination .page-jump-to > i {
  font-size: 16px;
  vertical-align: text-bottom;
  cursor: pointer;
  margin: 0 2px;
  color: #aaa;
}
.erp-pagination .page-jump-to > i:hover {
  color: #2790F8;
}
.erp-pagination .page-total > span {
  display: inline-block;
}
.erp-pagination .page-total::after {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.erp-pagination .page-total-page > span {
  vertical-align: middle;
}
.erp-pagination .text-middle {
  vertical-align: middle;
}
.erp-pagination .redo-btn {
  font-size: 12px;
}
.erp-pagination .redo-btn:hover {
  color: #2790F8;
}
.ant-select-sm .ant-select-selection__rendered {
  margin: 0 4px;
}
.ant-select-sm .ant-select-selection__clear,
.ant-select-sm .ant-select-arrow {
  right: 4px;
}
.ant-input-group-addon {
  padding: 0 6px;
}
.ant-input-sm {
  padding: 1px 4px;
}
input[readonly]:hover,
input[readonly]:focus {
  box-shadow: none;
  border-color: #d9d9d9;
}
